___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: #4361ee;
  --secondary-color: #3f37c9;
  --accent-color: #4895ef;
  --light-color: #f8f9fa;
  --dark-color: #212529;
  --success-color: #4cc9f0;
  --warning-color: #f72585;
  --border-radius: 8px;
  --box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  --transition: all 0.3s ease;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

body {
  background-color: #f5f7fa;
  color: var(--dark-color);
  line-height: 1.6;
}

.container {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 2rem;
  padding: 1rem;
  max-width: 1400px;
  margin: 0 auto;
}

.section-title {
  margin: 1.5rem 0 1rem;
  color: var(--secondary-color);
  font-weight: 600;
}

.notes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.empty-state {
  grid-column: 1 / -1;
  text-align: center;
  color: #666;
  font-style: italic;
  padding: 2rem;
}

/* Loading Indicator */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.loading-spinner {
  background: white;
  padding: 2rem;
  border-radius: var(--border-radius);
  text-align: center;
  box-shadow: var(--box-shadow);
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 1rem;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* App Bar */
.app-bar {
  background: white;
  box-shadow: var(--box-shadow);
  padding: 1rem 0;
  margin-bottom: 1rem;
}

.app-bar__inner {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.app-bar__title {
  color: var(--primary-color);
  font-size: 1.5rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.theme-toggle {
  background: none;
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
  padding: 0.5rem;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.theme-toggle:hover {
  background: var(--primary-color);
  color: white;
}

@media (max-width: 768px) {
  .container {
    grid-template-columns: 1fr;
  }

  .notes-grid {
    grid-template-columns: 1fr;
  }
}

/* Form styles */
form {
  background: white;
  padding: 1.5rem;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  margin-bottom: 2rem;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: var(--secondary-color);
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: var(--border-radius);
  font-size: 1rem;
  transition: var(--transition);
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--accent-color);
  box-shadow: 0 0 0 2px rgba(72, 149, 239, 0.2);
}

.form-group textarea {
  min-height: 120px;
  resize: vertical;
}

.char-counter {
  font-size: 0.8rem;
  color: #666;
  text-align: right;
  margin-top: 0.25rem;
}

.invalid {
  color: var(--warning-color);
}

button {
  background-color: var(--primary-color);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: var(--border-radius);
  cursor: pointer;
  font-size: 1rem;
  font-weight: 500;
  transition: var(--transition);
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

button:hover {
  background-color: var(--secondary-color);
}

button.secondary {
  background-color: #6c757d;
}

button.secondary:hover {
  background-color: #5a6268;
}

button.danger {
  background-color: var(--warning-color);
}

button.danger:hover {
  background-color: #d91a63;
}

.form-actions {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

/* Note item styles */
.note-item {
  background: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  padding: 1.5rem;
  transition: var(--transition);
  display: flex;
  flex-direction: column;
  height: 100%;
}

.note-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
}

.note-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 0.75rem;
  color: var(--secondary-color);
}

.note-body {
  flex-grow: 1;
  margin-bottom: 1rem;
  color: #555;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  line-clamp: 3;
}

.note-date {
  font-size: 0.8rem;
  color: #888;
  margin-bottom: 1rem;
}

.note-actions {
  display: flex;
  gap: 0.5rem;
  margin-top: auto;
}

/* Sidebar styles */
.sidebar {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.category-list {
  background: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  padding: 1rem;
}

.category-item {
  padding: 0.75rem 1rem;
  border-radius: var(--border-radius);
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  transition: var(--transition);
  margin-bottom: 0.5rem;
}

.category-item:hover {
  background-color: #f0f2f5;
}

.category-item.active {
  background-color: #e6f0ff;
  color: var(--primary-color);
  font-weight: 500;
}

.category-item i {
  width: 20px;
  text-align: center;
}

.search-box {
  background: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  padding: 1rem;
}

.search-input {
  position: relative;
}

.search-input i {
  position: absolute;
  left: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  color: #666;
}

.search-box input {
  width: 100%;
  padding: 0.75rem 0.75rem 0.75rem 2.5rem;
  border: 1px solid #ddd;
  border-radius: var(--border-radius);
  font-size: 1rem;
  transition: var(--transition);
}

.search-box input:focus {
  outline: none;
  border-color: var(--accent-color);
}

/* Dark theme styles */
.dark-theme {
  background-color: #121212;
  color: #e0e0e0;
}

.dark-theme .app-bar {
  background-color: #1e1e1e;
}

.dark-theme .note-item,
.dark-theme .category-list,
.dark-theme .search-box,
.dark-theme form {
  background-color: #1e1e1e;
  color: #e0e0e0;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}

.dark-theme .form-group input,
.dark-theme .form-group textarea,
.dark-theme .search-box input {
  background-color: #2d2d2d;
  color: #e0e0e0;
  border-color: #444;
}

.dark-theme .note-title {
  color: #64b5f6;
}

.dark-theme .section-title {
  color: #bb86fc;
}

.dark-theme .category-item:hover {
  background-color: #333;
}

.dark-theme .category-item.active {
  background-color: #3700b3;
  color: white;
}

.dark-theme .char-counter {
  color: #aaa;
}

.dark-theme .loading-spinner {
  background-color: #1e1e1e;
  color: #e0e0e0;
}

.dark-theme .app-bar__title {
  color: #64b5f6;
}

.dark-theme .theme-toggle {
  border-color: #64b5f6;
  color: #64b5f6;
}

.dark-theme .theme-toggle:hover {
  background: #64b5f6;
  color: #121212;
}
